
.navbar-fixed{
	position: -webkit-sticky;
	position: sticky;
	left:0px;
  	top:0px;
  	z-index:2;
}

.navbar-white{
	background-color: white; 
	border:5px solid black;
}
.navbrand{
	font-size: 24px;
	color:black;
}
.navbrand:hover{
	color: black;
}
.navlink{
	font-size: 18px;
	color:black;
}
@media (max-width: 876px){
	.navlink{
		font-size: 16px;
	}
	.navbrand{
		font-size: 20px;
	}
}
@media (max-width: 822px){
	.navlink{
		font-size: 15px;
	}
	.navbrand{
		font-size: 18px;
	}
}
.navlink:hover{
	color:black;
	background: -webkit-gradient(linear, left top, left bottom, from(#AEF57F), to(#95BD86)) !important;
	background: -webkit-linear-gradient(#AEF57F, #95BD86) !important;
	background: -o-linear-gradient(#AEF57F, #95BD86) !important;
	background: linear-gradient(#AEF57F, #95BD86) !important;
}
.navlink:focus{
	color:black;
	background: -webkit-gradient(linear, left top, left bottom, from(#AEF57F), to(#95BD86)) !important;
	background: -webkit-linear-gradient(#AEF57F, #95BD86) !important;
	background: -o-linear-gradient(#AEF57F, #95BD86) !important;
	background: linear-gradient(#AEF57F, #95BD86) !important;
}
.HeaderDogContainer{
	border: 1px black solid;
	border-radius: 5px;
	background: -webkit-gradient(linear, left top, left bottom, from(#AEF57F), to(#95BD86));
	background: -webkit-linear-gradient(#AEF57F, #95BD86);
	background: -o-linear-gradient(#AEF57F, #95BD86);
	background: linear-gradient(#AEF57F, #95BD86);
	margin-top: 3px;
	width: 200px;
	height: 44px;
}
.element_0_headerDog{
	margin-top: 13px;
}
.element_1_headerDog{
	margin-top: 2px;
}
.element_2_headerDog{
	margin-top: -7px;
}

.HeaderSearchContainer{
	margin-top: 10px;
	border-radius: 5px;
}


